'use client';

import { useEffect, useState } from 'react';

type Event = {
  id: string;
  type: string;
  element: string;
  timestamp: number;
};

export function EventLog() {
  const [events, setEvents] = useState<Event[]>([]);

  useEffect(() => {
    const handleCustomEvent = (e: CustomEvent<Event>) => {
      setEvents(prev => [...prev, e.detail]);
    };

    window.addEventListener('analytics-event' as any, handleCustomEvent);
    return () => window.removeEventListener('analytics-event' as any, handleCustomEvent);
  }, []);

  return (
    <div className="bg-gray-50 rounded-lg p-6 border border-gray-200">
      <h2 className="text-lg font-semibold mb-4">Event Log</h2>
      <div className="space-y-2 max-h-[500px] overflow-y-auto">
        {events.map((event) => (
          <div key={event.id} className="bg-white p-3 rounded border border-gray-100 text-sm">
            <div className="flex justify-between text-gray-600">
              <span>{event.type}</span>
              <span>{new Date(event.timestamp).toLocaleTimeString()}</span>
            </div>
            <div className="text-gray-800 mt-1">{event.element}</div>
          </div>
        ))}
        {events.length === 0 && (
          <p className="text-gray-500 text-center py-4">No events recorded yet</p>
        )}
      </div>
    </div>
  );
}