"use client";

import { useState } from "react";
import { useAnalytics } from "@/hooks/useAnalytics";
import { StepIndicator } from "./StepIndicator";
import { StepFields } from "./StepFields";

const steps = [
  {
    id: "personal",
    title: "Personal Information",
    fields: [
      { name: "firstName", label: "First Name", type: "text" },
      { name: "lastName", label: "Last Name", type: "text" },
    ],
  },
  {
    id: "contact",
    title: "Contact Details",
    fields: [
      { name: "email", label: "Email", type: "email" },
      { name: "phone", label: "Phone", type: "tel" },
    ],
  },
  {
    id: "company",
    title: "Company Information",
    fields: [
      { name: "company", label: "Company Name", type: "text" },
      { name: "role", label: "Job Title", type: "text" },
    ],
  },
  {
    id: "preferences",
    title: "Preferences",
    fields: [
      {
        name: "newsletter",
        label: "Subscribe to newsletter",
        type: "checkbox",
      },
      { name: "updates", label: "Receive product updates", type: "checkbox" },
    ],
  },
];

export function MultiStepForm() {
  const [currentStep, setCurrentStep] = useState(0);
  const { trackEvent } = useAnalytics();

  const handleNext = () => {
    trackEvent("button_click", "Next Step Button");
    setCurrentStep((prev) => Math.min(prev + 1, steps.length - 1));
  };

  const handlePrevious = () => {
    trackEvent("button_click", "Previous Step Button");
    setCurrentStep((prev) => Math.max(prev - 1, 0));
  };

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    trackEvent("form_submit", "Multi-step Form Submission");
  };

  return (
    <div className="bg-white rounded-lg p-6 border border-gray-200">
      <StepIndicator steps={steps} currentStep={currentStep} />

      <form onSubmit={handleSubmit}>
        <StepFields
          fields={steps[currentStep].fields}
          trackEvent={trackEvent}
        />

        <div className="mt-8 flex justify-between">
          <button
            type="button"
            onClick={handlePrevious}
            disabled={currentStep === 0}
            className="px-4 py-2 text-sm font-medium text-gray-700 bg-white border border-gray-300 rounded-md hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 disabled:opacity-50"
          >
            Previous
          </button>
          {currentStep === steps.length - 1 ? (
            <button
              type="submit"
              className="px-4 py-2 text-sm font-medium text-white bg-blue-600 border border-transparent rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
            >
              Submit
            </button>
          ) : (
            <button
              type="button"
              onClick={handleNext}
              className="px-4 py-2 text-sm font-medium text-white bg-blue-600 border border-transparent rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
            >
              Next
            </button>
          )}
        </div>
      </form>
    </div>
  );
}
